"use client";

import { useState } from "react";
import type { SupportedLocale } from "@/data/translations";
import { getTranslations } from "@/data/translations";
import { CORE_FAQS } from "@/data/faqs";

export function FaqList({ locale }: { locale: SupportedLocale }) {
  const [openIndex, setOpenIndex] = useState<number | null>(0);
  const t = getTranslations(locale);

  return (
    <section className="mx-auto max-w-5xl space-y-10">
      <header className="text-center space-y-4">
        <h2 className="text-3xl font-semibold text-slate-900">{t.faq.heading}</h2>
        <p className="mx-auto max-w-2xl text-base text-slate-500">{t.faq.intro}</p>
      </header>
      <div className="grid gap-4">
        {CORE_FAQS.map((faq, index) => {
          const isOpen = openIndex === index;
          const question = faq.question[locale];
          const answer = faq.answer[locale];
          return (
            <article
              key={`${faq.question.en}-${index}`}
              className="rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm"
            >
              <button
                type="button"
                onClick={() => setOpenIndex(isOpen ? null : index)}
                className="flex w-full items-center justify-between gap-4 text-left"
                aria-expanded={isOpen}
              >
                <h3 className="text-lg font-semibold text-slate-800">{question}</h3>
                <span className="text-2xl text-blue-500" aria-hidden>
                  {isOpen ? "−" : "+"}
                </span>
              </button>
              {isOpen && (
                <p className="mt-4 text-sm text-slate-500 leading-7">{answer}</p>
              )}
            </article>
          );
        })}
      </div>
    </section>
  );
}
